:root {
	--primary: #ff9751;
	--primary-variant:
		--secondary: #ffdf53;
}

@keyframes rocket-anim {
	0% { transform: translate(70vw, 1000vh) }
	100% { transform: translate(70vw, -2000vh) }
}

body {
	font-family: sans-serif;
	width: 90%;
	margin: 0 auto 0 auto;
	color: #DDD;
	background: url(./img/spacetile.png) repeat;

	display: flex;
	flex-direction: column;
	height: 100vh;
}

.contents {
	overflow-y: scroll;
}

ul.navbar {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: #333;
	border-radius: 8px;
}

ul.navbar > li {
	float: left;
}

a {
	text-decoration: none;
}

a:visited {
	color: #fc2828;
}

a:link, a:active {
	color: #ffd54f;
}

ul.navbar > li > a {
	display: block;
	padding: 8px 16px;
	text-decoration: none;
	text-align: center;
	color: #CCC;
}

ul.navbar > li > a:hover:not(.active) {
	background-color: #555;
}

ul.navbar > li > a.active {
	/* background: #04AA6d; */
	background: #16bce5;
	color: #333;
}
